<template>
 <div class="center"  id="paperAnalysis">
    <div class="center_breadcrumb">
         <Crumb @headInfo="headInfo"/>
    </div>
    <div class="center_content">
        <div class="center_content_search">
            <p class="t_p">请请先按条件查询您所要查询的课程、学年、班级</p>
            <div class="center_content_search_left t_s">
                <div class="t_box">
                    <div class="search-input-suffix gap">
                        <label>课程名称：</label>
                        <el-select v-model="page.courseId"  placeholder="请选择要查询的课程">
                            <el-option
                            v-for="item in curriculumOptions"
                            :key="item.id"
                            :label="item.name"
                            :value="item.id">
                            </el-option>
                        </el-select>
                    </div>
                    <div class="search-input-suffix gap" style="margin-left:60px; margin-right:60px">
                        <label>所属学年：</label>
                        <el-select v-model="page.grade" @change="gradeSelect" placeholder="请选择所属学年">
                            <el-option
                            v-for="item in gradeOptions"
                            :key="item"
                            :label="item"
                            :value="item">
                            </el-option>
                        </el-select>
                    </div>
                    <div class="search-input-suffix gap">
                        <label>所属班级：</label>
                        <el-select v-model="page.className" placeholder="请先选择所属学年">
                            <el-option
                            v-for="item in classOptions"
                            :key="item"
                            :label="item"
                            :value="item">
                            </el-option>
                        </el-select>
                    </div>
                </div>
            </div>
            <div class="t_b">
                <span @click ="search">查 询</span>
            </div>
        </div>
        <div v-if="!isShow" class="defaultPage">
          <img src="@/assets/img/img-exan.png" alt="">
          <p>选择课程、学年、班级查看试卷分析</p>
        </div>
        <div v-if="isShow" class="paperAnalysis_container">
            <div class="paperAnalysis_tab">
                <span class="t_btn active">汽车试验与测试技术</span>
            </div>
            <div class="paperAnalysis_body">
               <div class="export-box">
                   <span><SvgIcon icon-class="icondaochu-word-hui"></SvgIcon>&nbsp;导出Word</span>
                   <span><SvgIcon icon-class="icondaochu-excel-hui"></SvgIcon>&nbsp;导出Excel</span>   
               </div>
               <h3 class="hActive">试卷（卷面）分析</h3>
               <form @change="emptyOutBlur($event)" onsubmit="return false;"
                action="###"><p v-html="emptyOut(gapFilling)" class="gapFilling"></p></form>
               <h3>试卷（卷面）成绩分析统计</h3>
               <el-table :data="analyzeTableData" border class="cc-table" style="width: 100%;">
                    <el-table-column label="课程名称" prop="courseName"></el-table-column>
                    <el-table-column label="人数" prop="count"></el-table-column>
                    <el-table-column label="90-100">
                        <template slot-scope="scope">
                            {{`${scope.row.personCount1}人 {占${scope.row.proportion1}%}`}}
                        </template>
                    </el-table-column>
                    <el-table-column label="80-89">
                        <template slot-scope="scope">
                            {{`${scope.row.personCount2}人 {占${scope.row.proportion2}%}`}}
                        </template>
                    </el-table-column>
                    <el-table-column label="70-79">
                        <template slot-scope="scope">
                             {{`${scope.row.personCount3}人 {占${scope.row.proportion3}%}`}}
                        </template>
                    </el-table-column>
                    <el-table-column label="60-69">
                        <template slot-scope="scope">
                             {{`${scope.row.personCount4}人 {占${scope.row.proportion4}%}`}}
                        </template>
                    </el-table-column>
                     <el-table-column label="不及格">
                        <template slot-scope="scope">
                             {{`${scope.row.personCount5}人 {占${scope.row.proportion5}%}`}}
                        </template>
                    </el-table-column>
                     <el-table-column label="平均分">
                        <template slot-scope="scope">
                            {{scope.row.average}}分
                        </template>
                    </el-table-column>
                </el-table>
               <h3>试卷（卷面）批改的一致性检查</h3>
               <el-table :data="examineTableData" border class="cc-table" style="width: 100%;">
                     <el-table-column label="一致性检查">
                        <template slot-scope="scope">
                             {{`${scope.$index +1}、${scope.row.title}`}}
                        </template>
                    </el-table-column>
                     <el-table-column label="选项">
                        <template slot-scope="scope">
                            <el-radio-group v-model="scope.row.checkVal">
                                <el-radio v-for="(vo, index) in scope.row.options" :label="vo.id" :key="index">{{vo.name}}</el-radio>
                            </el-radio-group>
                        </template>
                    </el-table-column>
                </el-table>
               <h3>试卷（卷面）反应教与学存在的问题，有何改进措施？</h3>
               <el-input v-model="form.remark"  type="textarea" :rows="9" placeholder="改进措施实施方案" class="paperAnalysis-textarea"></el-input>
               <el-row :gutter="24">
                <el-col :span="6" :offset="9">
                    <span class="submit--btn">提交</span>
                </el-col>
            </el-row>
            </div>
        </div>
    </div>
 </div>
</template>

<script>
import teacherApi from '@/api/teacherApi';

export default {
 name: 'PaperAnalysis',
 components: {},
 data() {
 return {
    pageTitle:'试卷分析',
    curriculumOptions:[],
    gradeOptions:[],
    classOptions:[],
    gapFilling:'本试卷中共有@道大题，@道小题，包含了教学大纲中绝大多数重要的知识点，覆盖大纲知识点占@%，试题形式按照学校要求，注重考查学生对@知识点@方法的掌握和@能力的培养，难易程度@。考试试题中知识题占@%，能力题占@%。',
    analyzeTableData:[
        {
          courseName:'汽车试验与测试技术',
          count:20,
          personCount1:2,
          proportion1:6.7,
          personCount2:2,
          proportion2:6.7,
          personCount3:2,
          proportion3:6.7,
          personCount4:2,
          proportion4:6.7,
          personCount5:2,
          proportion5:6.7,
          average:86
        }
    ],
    examineTableData:[
        {
            title:'是否按照评分标准改卷',
            checkVal:'',
            options:[
                {
                    name:'是',
                    id:'1'
                },
                {
                    name:'否',
                    id:'2'
                },
            ]
        },
        {
            title:'同一门课，各班改卷方式为：',
            checkVal:'',
            options:[
                {
                    name:'集中流水改卷',
                    id:'1'
                },
                {
                    name:'分班独立改卷',
                    id:'2'
                },
            ]
        },
        {
            title:'分班改卷是否执行统一的评分标准：',
            checkVal:'',
            options:[
                {
                    name:'是',
                    id:'1'
                },
                {
                    name:'否',
                    id:'2'
                },
            ]
        }
    ],
    page:{
        courseId:'',
        grade:'',
        className:'',
        versionId:localStorage.getItem('versionId')
    },
    isShow:false,
    form:{
        proposal:'',
        courseSchedulingId:'',
        remark:''
    }
   
 }
 },
 created(){},
 mounted(){
     this.scoreAnalysisGrade()
 },
 methods:{ 
     initData(){
         const url =teacherApi.scoreAnalysisSelectScore(this.page);
        //  this.$get(url).then(res =>{
        //      if(res.length === 0){
        //           this.isShow=false;
        //           this.$notify.info({
        //             duration:1500,
        //             message: '未查到您所需要的内容'
        //          })
                   
        //      }else{
        //         this.isShow=true
        //         this.isScatterShow=true
        //         this.activeIndex=0
        //         this.list=res
        //         this.tableData=res[0]
        //         this.form.proposal=this.tableData.proposal  
        //         this.form.courseSchedulingId=this.tableData.courseSchedulingId
        //         this.scatterData=this.tableData.scoreGaols  
        //         this.initEchartData()
        //      }        
            
        //  })
     },   
     //所属学年
     scoreAnalysisGrade(){
         //const url =teacherApi.scoreAnalysisGrade;
         const versionId= localStorage.getItem('versionId')
         const url=`/teacher/scoreAnalysis/grade/${versionId}`
         this.$get(url).then(res =>{
              this.gradeOptions=res;
         })
     },
     //所属班级
     scoreAnalysisClass(gradeName){
         const url =teacherApi.scoreAnalysisClass;
         const postData={
             grade:gradeName
         }
         this.$get(url, postData).then(res =>{
              this.classOptions=res;
         })
     },
     gradeSelect(){
        this.page.className=''
        this.scoreAnalysisClass(this.page.grade) 
     },
     search(){
       this.initData()
       this.isShow=!this.isShow
     },
    headInfo(item){
        this.pageTitle=item[1]
     },
     emptyOutBlur(e) { 
        const formList = e.target.form;
        console.log('填空内容', e.target.form)
        const valArr = [];
        for(var i = 0; i < formList.length; i++) {
            valArr.push(formList[i].value);
        } 
        const valStr = valArr.join(',');
        console.log(valStr)

    },
    emptyOut(value) { 
        const reg = /@/g
        return value.replace(reg, '<input class="emptyOut el-input__inner" type="text"  placeholder="请填写" onpaste="return false" oncontextmenu="return false"/>');
    },
 }
}
</script>
<style lang='scss'>
  @import '../../styles/center.scss';
  #paperAnalysis{
      .defaultPage{
        padding: 50px 0 150px;
        box-shadow:inherit;
        p{
            font-size:20px;font-family:PingFang SC;font-weight:bold;color:rgba(3,177,135,1);margin-top: 49px;
        }
      }
      .center_content{
          border-radius:10px;
          .center_content_search{
               height:280px;
               background-image:url('../../assets/img/bg-chaxun.png')!important;
               background-size: 100% 100%;
               border-radius:10px 10px 0px 0px;
               margin-bottom:0;
               padding-top:0;
               .t_p{
                    font-size:24px;
                    font-weight:bold;
                    color:#fff;
                    margin-top: 30px;
                    text-align:center;
                    margin-bottom: 59px;
               }
               .t_s{
                height:42px;
                width:100%;
                float: none;
                padding-left: 0;
                position:relative;
                .t_box{
                    height:100%;
                    width:100%;
                    display:flex;
                    justify-content: center;
                    align-items:center;
                   .search-input-suffix{
                      label{
                          font-size:16px;
                          font-weight:bold;
                          color:#fff;
                      }
                      .el-input__inner{
                          border:1px solid #fff;
                          color:#fff;
                          background: transparent;
                          font-size:14px;
                      }
                      input::placeholder{
                            color:#fff;
                        }
                      .el-select__caret{
                            color:#fff;
                      }
                    }
                }
                
               }
               .t_b{
                   height:50px;
                   position:relative;
                   margin-top:40px;
                   span{
                       display:inline-block;
                       width:270px;
                       height:50px;
                       background:rgba(255,255,255,1);
                       border-radius:25px;
                       text-align:center;
                       font-size:18px;
                        font-weight:bold;
                        color:#03B187;
                        line-height:50px;
                        position:absolute;
                        left:0;
                        right:0;
                        top:0;
                        bottom:0;
                        margin:auto;
                        cursor:pointer;
                   }
               }
          }
      }
      .paperAnalysis_container{
         .paperAnalysis_tab{
            width:100%;
            min-height:37px;
            background:#f8f8f8;
            text-align: left;
            clear: both;
            padding: 30px 0;
            .t_btn{
                display:inline-block;
                font-size:14px;
                color:#696969;
                padding: 10px 16px;
                background: transparent;     
                border-radius:20px;
                margin-bottom: 10px;
                cursor:pointer;
            }
            .active{
              color:#fff;
              background:rgba(3,177,135,1);
              box-shadow:0px 2px 20px 0px rgba(3,177,135,0.3);
            }  
        }
        .paperAnalysis_body{
            background:#fff;
            border-radius:10px;
            box-sizing:border-box;
            padding:30px;
            position:relative;
            .export-box{
                height:28px;
                width:261px;
                position:absolute;
                top:30px;
                right:30px;
                display:flex;
                justify-content: space-between;
                align-items: center;
                span{
                    width:120px;
                    height:28px;
                    border:1px solid rgba(238,238,238,1);
                    border-radius:14px;
                    display:flex;
                    justify-content: center;
                    align-items: center;
                    font-size:14px;
                    color:#ccc;
                    cursor:pointer;
                    .svg-icon{
                        color:#ccc;
                         cursor:pointer;
                    }
                }
            }
            h3{
              font-size:20px;
              color:#323232;
              margin:40px 0 30px 0;
            }
            .hActive{
                margin:0 0 30px 0;
            }
            .gapFilling{
                font-size:14px;
                color:#323232;
                line-height:60px;
                 .emptyOut{
                    max-width: 200px;
                    margin:0 10px;
                    background:#F8F8F8;
                }
            }
            .paperAnalysis-textarea{
                 margin-bottom:30px;
                .el-textarea__inner{
                     background:#F8F8F8;
                }     
            }
        }
      }
  }
</style>



